import { ReactComponent as Calendar } from '../../../images/calendar.svg';
import { ReactComponent as Calculator } from '../../../images/calculator.svg';
import { DatePicker, Input, Button } from 'antd';
import { Field, Form, Formik } from 'formik';
import { FormContainer } from '../ExpensesPage.styled';
import { TablePage } from '../TablePage/TablePage';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import { useTransactions } from 'hooks/useTransactions';
import { Select } from 'antd';
import format from 'date-fns/format';
dayjs.extend(customParseFormat);

const dateFormat = 'DD.MM.YYYY';
const calendarIcon = <Calendar />;
const calculatorIcon = <Calculator />;

export const InputForm = () => {
  const { categories } = useTransactions();
  const items = categories.map((e, index) => {
    return { value: e, label: e };
  });
  const initialValues = {
    description: '',
    amount: '',
    date: '',
    category: '',
  };

  const onSubmit = (value, actions) => {
    const query = { ...value };
    let date = new Date(value.date.toString());
    date = format(date, 'yyyy-MM-dd');
    query.date = date;
    console.log(query);
  };
  return (
    <FormContainer>
      <div style={{ display: 'flex' }}>
        <Formik initialValues={initialValues} onSubmit={onSubmit}>
          {({
            values,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
          }) => (
            <Form onSubmit={handleSubmit}>
              <Field name="date">
                {({ field }) => (
                  <DatePicker
                    {...field}
                    onChange={date =>
                      handleChange({
                        target: {
                          name: 'date',
                          value: date,
                        },
                      })
                    }
                    onBlur={handleBlur}
                    defaultValue={dayjs()}
                    format={dateFormat}
                    bordered={false}
                    suffixIcon={calendarIcon}
                    size="middle"
                    name="date"
                    label="date"
                  />
                )}
              </Field>
              <Field name="description">
                {({ field }) => (
                  <Input
                    {...field}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    className="productdesc"
                    placeholder="Product description"
                  />
                )}
              </Field>
              <div style={{ width: '165px' }}>
                <Field
                  style={{
                    width: 200,
                  }}
                  name="category"
                  as={Select}
                  placeholder="Select a value"
                  onChange={value => setFieldValue('category', value)}
                  value={values.dropBox}
                  options={items}
                ></Field>
              </div>
              <Field name="amount">
                {({ field }) => (
                  <Input
                    {...field}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    suffix={calculatorIcon}
                    placeholder="0,00"
                    className="calc"
                    style={{ marginRight: '32px' }}
                  />
                )}
              </Field>
              <Button
                type="primary"
                htmlType="submit"
                style={{
                  marginRight: '16px',
                  width: '136px',
                  height: '44px',
                  backgroundColor: '#FF751D',
                }}
                className="submitBtn"
              >
                Input
              </Button>
              <Button style={{ width: '136px', height: '44px' }}>Clear</Button>
            </Form>
          )}
        </Formik>
      </div>
      <TablePage />
    </FormContainer>
  );
};
